/**@jsx jsx*/
import {useState, useEffect} from 'react'
import {jsx, css} from '@emotion/core'
import { AgGridColumn, AgGridReact } from 'ag-grid-react';
import axios from 'axios'

import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';

const LocationsPage =  () => {
  const [gridApi, setGridApi] = useState(null);
  const [gridColumnApi, setGridColumnApi] = useState(null);
  const [rowData, setRowData] = useState([])

  useEffect(() => {
    const fetchData = async () => {
      try {
        const {data} = await axios.get('http://netcmdb-loc.rs.ru:8082/locations/officeTable2.json')
        setRowData(data && data.res)
      } catch (e) {
        console.log(e)
      }
    }
    fetchData().then(() => {})
  }, [])

  function onGridReady(params) {
    setGridApi(params.api);
    setGridColumnApi(params.columnApi);
  }

  return (
    <div className="ag-theme-alpine" style={ { height: '100%', width: '100%' } }>
      <AgGridReact
        onGridReady={onGridReady}
        rowData={rowData}>
        <AgGridColumn field="regCenter" />
        <AgGridColumn field="region" />
        <AgGridColumn field="city"/>
        <AgGridColumn field="address" />
        <AgGridColumn field="office" />
      </AgGridReact>
    </div>
  );

}

export default LocationsPage